.searchform {
  @media (--medium) {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  & label {
    flex: 1;
    text-align: end;
  }
  & input {
    flex: 4;
    margin-inline-start: 10px;
  }
  & button {
    flex: 1;
    margin-inline-start: 10px;
  }
}